	import{ mapState,mapMutations} from 'vuex'
export default {
	data() {
		return {
			buttonStart:false,
			times:'',
			startTime:'',
			endTime:'',
			duration:'',
			timeHour :'', // 小时
			timeMinute :  '',// 分钟
			timeSecond :'', // 秒
			hour:'', // 小时
			minute:'', // 分钟
			second:'', // 秒
			strtime : '2020-05-21 00:00:00:000',
			timeCount:'',
			dayData:'00:00:00',
			param:{
				'pictures':[],
			},
			pushId:'',
			startTimes:'',
			stTimes:'',
			endTimes:'',
			videodata:'',
			
		};
	},
	computed: {
