	methods: {
		submitFrom(e){
			var start=undefined;
			var end=undefined;
			if(!this.hasLogin){
				uni.navigateTo({
					url: '../login/login' 
				});
				return false;
			}
			if(this.endTimes!=''){
				var start=this.stTimes;
				var end=this.endTimes;
				
			};
			if(e.detail.value.content==''){
				uni.showToast({
				    title: '描述不能为空',
					mask:true,
				    duration: 1000,
					image:'/static/images/icon/clock.png'
				});
