						extendContent:e.detail.value.extendContent,
						pictures:this.param.pictures,
						startTime:start,
						endTime:end,
					},true).then(res=>{
						var data ={
							pushId:this.pushId,
							cardId:res.obj
						}
						uni.showToast({
							title: '保存成功',
							icon: 'success',
							duration: 1500,
							success() {
								uni.reLaunch({
									url: '../index/cardDetails/cardDetails?pushList='+encodeURIComponent(JSON.stringify(data))
								})
							}
						});
						
					})
				}else{
					uni.showToast({
					    title: '内容包含敏感内容',
						mask:true,
					    duration: 2000,
						
					});
					return false
				}
				  
			})
			// if(this.param.pictures==''){
			// 	uni.showToast({
			// 	    title: '请上传图片',
			// 		mask:true,
			// 	    duration: 1000,
			// 		image:'/static/images/icon/clock.png'
			// 	});
			// 	return false
			// }
			// if(e.detail.value.extendContent==''){
			// 	uni.showToast({
			// 	    title: '概述不能为空',
			// 		mask:true,
			// 	    duration: 1000,
			// 		image:'/static/images/icon/clock.png'
			// 	});
			// 	return false
			// }			 
			 // var pictures=JSON.stringify( this.param.pictures);
			 // console.log(pictures)
			 
			
			
		},
		popUpImg(){
			const that = this;
			uni.chooseImage({
			    count: 1, //默认9
			    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['album'], //从相册选择
			    success: function (res) {
					let tempFilePaths = res.tempFilePaths;
					that.xdUniUtils.xd_request_img(res.tempFilePaths[0]).then(res=>{
